<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" ref="getQuery"></QueryBox>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 下发任务列表-->
    <div class="spotsTable" :class="{addActive:addActive}">
      <plaqueList ref="plaqueList" @signOut="signOut" :taskId="taskId"></plaqueList>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import plaqueList from './plaqueList'
import {
  // 表格列表
  getTaskList
} from '@/api/taskManager'
export default {
  name: 'taskScheduler',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格加载 */
      loading: false,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
        },
        // {
        //   title: '任务类型',
        //   align: 'center',
        //   key: 'cl004',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl004
        //     if (data === 0) {
        //       return h('span', {}, '巡护')
        //     } else if (data === 1) {
        //       return h('span', {}, '核查')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '任务年份',
          align: 'center',
          width: 100,
          key: 'cl010'
        },
        // {
        //   title: '任务级别',
        //   align: 'center',
        //   key: 'cl011',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl011
        //     if (data === 1) {
        //       return h('span', {}, '国家级')
        //     } else if (data === 2) {
        //       return h('span', {}, '市级')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '任务台账',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          render: (h, params) => {
            let row = params.row
            let optionArr = []
            row.rlhdGroupList.forEach(item => {
              optionArr.push(item.name)
            })
            return h('div', {}, optionArr.join('，'))
          }
        },
        {
          title: '任务描述',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        },
        {
          title: '操作',
          key: '',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 任务调度
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                on: {
                  click: () => {
                    this.addActive = true
                    this.taskId = params.row.cl001
                    this.$refs.plaqueList.getQlaques(params.row.cl001)
                  }
                }
              }, '任务调度')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      /* 初始化页码 */
      pageNum: 1,
      /* 页容量自适应 */
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 下发任务弹框状态 */
      addActive: false,
      taskId: ''
    }
  },
  components: {
    QueryBox,
    // 斑块列表
    plaqueList
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'cl002',
          title: '任务名称'
        },
        // {
        //   type: 2,
        //   field: 'cl004',
        //   title: '任务类型',
        //   data: [
        //     {
        //       value: '',
        //       label: '请选择'
        //     },
        //     {
        //       value: 0,
        //       label: '巡护'
        //     },
        //     {
        //       value: 1,
        //       label: '核查'
        //     }
        //   ]
        // },
        {
          type: 3,
          field: 'cl010',
          title: '任务年份'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getTaskList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px 0;
        text-align: right
      }
    }
    .spotsTable {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
