<template>
  <div id="issueList">
    <Modal
      id="taskIssueModal"
      v-model="issue"
      title="下发人员列表"
      :loading="modalIssueLoading"
      @on-cancel="handleCancel">
      <Form ref="formInline" inline :label-width="75" :model="formInline" class="formInline">
        <FormItem label="真实姓名：">
          <Input type="text" v-model="formInline.sa019" style="width: 150px;" placeholder="请输入用户名称"></Input>
        </FormItem>
        <FormItem label="手机号：">
          <Input type="text" v-model="formInline.sa012" style="width: 150px;" placeholder="请输入手机号"></Input>
        </FormItem>
        <FormItem :label-width="0" class="ml20">
          <Button type="primary" size="small" icon="ios-search" @click.stop="getSearchIssueList" class="mr10">查询</Button>
          <Button type="primary" size="small" icon="md-refresh" @click.stop="cleardata">重置</Button>
        </FormItem>
      </Form>
      <div>
        <Table border stripe :loading="IssueLoading" :columns="IssueColumns1" :data="IssueData" highlight-row :max-height="IssueTableHeight">
        </Table>
        <div class="page_box">
          <Page :total="IssueTotal" ref="IssuePages" :page-size="IssuePageSize" :current.sync="IssuePageNum" @on-change="IssuePageChange" show-total/>
        </div>
      </div>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>

<script>
import {
  // 表格列表
  userList,
  // 下发任务
  sharePoint
} from '@/api/taskManager'
export default {
  name: 'IssueList',
  data () {
    return {
      /* 弹框状态 */
      issue: false,
      /* 点击确定按钮时，确定按钮是否显示 loading 状态 */
      modalIssueLoading: false,
      /* 查询条件参数 */
      formInline: {
        sa019: '',
        sa012: ''
      },
      /* 表格高度 */
      IssueTableHeight: Math.floor((window.innerHeight - 350 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      IssueColumns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.IssuePageNum - 1) * this.IssuePageSize + 1)
          }
        },
        {
          title: '用户名',
          align: 'center',
          minWidth: 200,
          key: 'sa008'
        },
        {
          title: '手机号码',
          align: 'center',
          minWidth: 200,
          key: 'sa012'
        },
        {
          title: '真实姓名',
          align: 'center',
          minWidth: 200,
          key: 'sa019'
        },
        {
          title: '操作',
          key: '',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 下发斑块
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'ios-paper-plane'
                },
                style: {
                  background: '#2d579b',
                  borderColor: '#2d579b'
                },
                on: {
                  click: () => {
                    this.handleIssue(params.row.sa001)
                  }
                }
              }, '下发')
            ])
          }
        }
      ],
      IssueData: [],
      /* 总条数 */
      IssueTotal: 0,
      IssuePageNum: 1,
      IssuePageSize: 10,
      /* 加载 */
      IssueLoading: false
    }
  },
  props: {
    /* 斑块id */
    plaqueId: {},
    taskId: {}
  },
  created () {
    this.getIssueList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.IssueTableHeight = Math.floor((window.innerHeight - 350 - 40) / 48) * 48 + 40
      this.getIssueList()
    }, false)
  },
  methods: {
    /* 根据查询条件查询列表 */
    getSearchIssueList () {
      this.IssuePageNum = 1
      this.getIssueList()
    },
    /* 查询列表 */
    getIssueList () {
      this.IssueLoading = true
      userList({
        data: {
          pageSize: this.IssuePageSize,
          pageNumber: this.IssuePageNum,
          sa019: this.formInline.sa019,
          sa012: this.formInline.sa012
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.IssueData = res.data.data.rows
          this.IssueTotal = res.data.data.total
          this.IssueLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    IssuePageChange (pageNum) {
      this.IssuePageNum = pageNum
      this.getIssueList()
    },
    // 下发任务
    handleIssue (e) {
      sharePoint({
        data: {
          pointIdList: this.plaqueId,
          uidList: [e]
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '斑块下发成功' })
          this.handleCancel()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 清空查询数据 */
    cleardata () {
      this.formInline = {}
      this.getSearchIssueList()
    },
    handleCancel () {
      this.issue = false
      this.$emit('getQlaques', this.taskId)
      this.$emit('getQlaquePeoples')
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  .formInline {
    overflow: hidden;
    .ivu-form-item {
      margin-bottom: 16px;
    }
  }
  .mr10 {
    margin-right: 10px;
  }
  .ml20 {
    margin-left: 20px;
  }
  .page_box {
    margin-top: 15px;
    text-align: right;
  }
</style>
