import request from '@/utils/request'

/* 斑块列表  */
export const getQlaqueList = data => request({ url: '/ygjc/rlhdGroup/listReserveData', method: 'post', data })
/* 已下发斑块人员列表  */
export const getQlaquePeopleList = data => request({ url: '/ygjc/rlhdGroup/getPersonAndPoint', method: 'post', data })
/* 删除已下发斑块人员所属斑块  */
export const deletePersonAndPoint = data => request({ url: '/ygjc/rlhdGroup/deletePersonAndPoint', method: 'post', data })
/* 斑块列表  */
export const getPointFromStage = data => request({ url: '/ygjc/rlhdGroup/getPointFromStage', method: 'post', data })
