<template>
  <div id="tablets">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>斑块分配管理</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="content_box">
      <div class="demo-split" :style="{height:screenHeight+'px'}">
        <Split v-model="split1">
          <div slot="left" class="demo-split-pane">
            <Divider orientation="left" style="position: relative">未分配斑块列表<Button type="primary" size="small" icon="ios-paper-plane" class="btn-exportBtn" @click.stop="handleQlaque" style="position: absolute;top: -18px;right: 0px;">下发</Button></Divider>
            <Table id="fpbkList" :loading="plaqueLoading" size="small" :columns="plaqueColumns1" :data="plaqueData1" highlight-row :max-height="plaqueTableHeight"
                   ref="plaqueMultipleTable"
                   @on-selection-change="handleQlaqueSelectionChange">
            </Table>
          </div>
          <div slot="right" id="right" class="demo-split-pane">
            <Divider orientation="left">已下发斑块人员列表</Divider>
            <Collapse simple :accordion=true>
              <Panel v-for="(item, index) in plaquepeopleData1" :key="index">
                {{item.sa019}}
                <Tag color="primary">已下发斑块数量：{{ item.cdHelper.length }}</Tag>
                <div slot="content" id="content">
                  <b>所属斑块编码：</b>
                  <ul>
                    <li v-for="(i, ind) in item.cdHelper" :key="ind">
                      <span :title="i.active_name">{{i.cd001}}</span>
                      <Icon type="ios-trash" class="deleteBtn" @click="deleteSSBK(item.sa001, i.cd001)"/>
                    </li>
                  </ul>
                </div>
              </Panel>
            </Collapse>
          </div>
        </Split>
      </div>
    </div>
    <!-- 下发人员列表-->
    <IssueList ref="issueList" :plaqueId="plaqueId" :taskId="taskId" @getQlaques="getQlaques" @getQlaquePeoples="getQlaquePeoples"></IssueList>
  </div>
</template>

<script>
import IssueList from './IssueList'
import {
  // 斑块表格列表
  getQlaqueList,
  // 已下发人员列表
  getQlaquePeopleList,
  // 删除已下发斑块人员所属斑块
  deletePersonAndPoint
} from '@/api/taskScheduler'
import {
  // 下发任务
  sharePoint
} from '@/api/taskManager'
export default {
  name: 'plaqueList',
  data () {
    return {
      /* 表格高度 */
      plaqueTableHeight: window.innerHeight - 385,
      /* 表格加载 */
      plaqueLoading: false,
      /* 表格数据 */
      plaqueColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '斑块编码 ',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 120,
          key: 'cd001'
        },
        {
          title: '斑块名称 ',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 120,
          key: 'activeName'
        }
      ],
      plaqueData1: [],
      // 斑块id
      plaqueId: '',
      // 多选相关参数
      selectQlaqueRow: [],
      multipleQlaqueSelection: [],
      // 面板分隔
      split1: 0.5,
      screenHeight: window.innerHeight - 215,
      // 已下发人员列表
      plaquepeopleLoading: false,
      plaquepeopleData1: []
    }
  },
  components: {
    // 下发人员列表
    IssueList
  },
  watch: {
    multipleQlaqueSelection (data) { // 存储选中的row
      console.log(data)
      this.selectQlaqueRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectQlaqueRow.push(item.cd001)
        })
      }
    }
  },
  props: {
    taskId: {}
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.plaqueTableHeight = window.innerHeight - 385
      this.getQlaques(this.taskId)
    }, false)
    this.getQlaquePeoples()
  },
  methods: {
    /* 获取字段列表 */
    getQlaques (e) {
      this.multipleQlaqueSelection = []
      this.selectQlaqueRow = []
      this.plaqueLoading = true
      getQlaqueList({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.plaqueData1 = res.data.data.data
          this.plaqueLoading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取下发斑块人员列表 */
    getQlaquePeoples () {
      getQlaquePeopleList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.plaquepeopleData1 = res.data.data
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 勾选checkbox事件
    handleQlaqueSelectionChange (val) {
      this.multipleQlaqueSelection = val
    },
    // 显示下发人员列表
    handleQlaque () {
      if (this.selectQlaqueRow.length < 1) {
        this.$Notice.error({ title: '所选斑块不能为空！' })
      } else {
        this.plaqueId = this.selectQlaqueRow
        this.$refs.issueList.issue = true
      }
    },
    // 删除人员所对应的各个斑块
    deleteSSBK (e, m) {
      this.$Modal.confirm({
        title: '确认',
        content: '请问您确定要删除该斑块吗？',
        onOk: () => {
          deletePersonAndPoint({
            data: {
              uid: e,
              pointIdList: [m]
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '删除成功' })
              this.getQlaquePeoples()
              this.getQlaques(this.taskId)
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        },
        onCancel: () => {
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablets{
    position: relative;
    height: 100%;
    .queryBox {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .content_box {
      height: calc(100% - 80px);
      margin: 0 15px 0;
      .demo-split {
        max-height: 100%;
        overflow: auto;
        border: 1px solid #dcdee2;
        .demo-split-pane{
          padding: 10px;
          #fpbkList {
            width: calc(100% - 65px);
            margin: auto
          }
        }
        .right-pane {
          height: 100%;
          overflow: hidden;
          .demo-split-pane {
            height: 100%;
            overflow: hidden;
            background: #fff;
            .ivu-collapse-simple {
              height: calc(100% - 65px);
              overflow: auto;
              width: calc(100% - 65px);
              margin: auto;
              border: none !important;
              .ivu-collapse-header {
                overflow: hidden;
                .ivu-tag {
                  float: right;
                  margin-top: 9px;
                }
              }
              .ivu-collapse-content-box {
                #content {
                  overflow: hidden;
                  b {
                    float: left;
                    line-height: 28px;
                  }
                  ul {
                    float: left;
                    width: calc(100% - 160px);
                    margin-left: 15px;
                    li {
                      width: 100%;
                      overflow: hidden;
                      border-bottom: 1px dashed #f0f0f0;
                      line-height: 28px;
                      span {
                        float: left;
                        width: 70%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        text-align: left;
                      }
                      .deleteBtn {
                        float: right;
                        cursor: pointer;
                        color: #ed4014;
                        font-size: 20px;
                        vertical-align: middle;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      .pBtn {
        margin-top: 15px;
      }
      .page_box{
        width: 100%;
        padding: 15px 0;
        text-align: right
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
